@import url("https://fonts.googleapis.com/css2?family=Bungee+Hairline&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Annie+Use+Your+Telescope&display=swap');

.main-part {
    position: relative;
    width: 100vw;
    height: 245vh;
    background-color: rgba(95, 158, 160, 0.521);
    z-index: 1;
}

#projects::before {
    content: "";
    background-image: url("../public/preview-60266W89RdQZN4s_0005.jpg");
    width: 300%;
    height: 300%;
    top: -110%;
    left: -50%;
    opacity: 0.1;
    position: fixed;
    animation: grain 8s steps(10) infinite;
    z-index: 0;
}

.main-part-title {
    width: 100vw;
    padding-top: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #171717;
    color: #fafafa;
    font-family: "Bungee Hairline", cursive;
    animation:  color-changer 15s infinite linear;
    z-index: 2;
}

@keyframes color-changer {
    0% {
        color: #fafafa;
    }
    25% {
        color: rgba(95, 158, 160, 0.521);
    }
    50% {
        color: rgb(18, 241, 174);
    }
    75% {
        color: #0fc4e4;
    }
    100% {
        color: #fafafa;
    }
}

#projects {
    width: 100vw;
    height: 235vh;
    padding-top: 25px;
    padding-bottom: 150px;
    background-color: #171717;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
    align-content:space-around;
    z-index: 2;
}


.project {
    width: 40vw;
    height: 50vh;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    border-radius: 5px;
    transition: 500ms;
}

.project:nth-child(1)::after {
    content: "";
    width: 40vw;
    height: 40vh;
    position: absolute;
    top: 0;
    background-image: url("../public/issue-tracker.jpg");
    background-size: cover;
    transition: 500ms;
}

.project:nth-child(1):hover::after {
    opacity: 0.1;
}

