.project .project-details {
    width: 30vw;
    height: 40vh;
    display: flex;
    visibility: hidden;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    opacity: 0;
    transition: 1000ms;
    z-index: 2;
}

.project:hover .project-details {
    visibility: visible;
    opacity: 1;
}

.project-details .project-title h2 {
    color: #fafafa;
    font-family: "Bungee Hairline", cursive;
    opacity: 0;
    transition: 1500ms;
}

.project:hover .project-details .project-title h2 {
    opacity: 1;
}

.project-link {
    text-decoration: none;
    color: inherit;
    font-family: inherit;
}

.project-link::after {
    display:block;
    content: '';
    border-bottom: solid 2px #fafafa;  
    transform: scaleX(0);  
    transition: transform 250ms ease-in-out;
}

.project-link:hover::after {
    transform: scaleX(1);
}

.project-details .project-skills {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    justify-items: center;
    align-content: space-around;
    color: #fafafa;
    font-family: monospace;
}

.project-details .project-skills p {
    padding: 6px;
    margin: 5px;
    border: 2px solid #fafafa;
    border-radius: 5px;
    opacity: 0;
    transition: 1500ms;
}

.project:hover .project-details .project-skills p {
    opacity: 1;
}

.project-code {
    width: 30vw;
    height: 5vh;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;

}

.project-code a {
    margin: 0 15px;
    text-decoration: none;
    color: #fafafa;
    font-family: monospace;
    font-size: 16px;
}


.project-code a::after {
    display:block;
    content: '';
    border-bottom: solid 2px #fafafa;  
    transform: scaleX(0);  
    transition: transform 250ms ease-in-out;
}

.project-code a:hover::after {
    transform: scaleX(1);
}